* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f5f6f7;
}

.titulo {
  min-width: 100%;
  min-height: 50vh;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 55px;

}

.boton {
  min-width: 100%;
  min-height: 11vh;
  background-color: #a2aab35d;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
}

.boton p {
  background: transparent;
}

.boton button {
  border: none;
  background-color: rgba(128, 128, 128, 0.603);
  font-size: 15px;
  padding: 13px;
  border-radius: 10%;
  cursor: pointer;
}

.boton button:hover {
  background-color: rgba(128, 128, 128, 0.397) ;
}

.cont {
 padding-top: 30px;
 padding-bottom: 30px;
  min-width: 100%;
  background-color: rgb(54, 54, 54);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.subCont {
  width: 430px;
  margin: 10px;
  display: flex;
 align-items: center;
 overflow: hidden;
 border-radius: 10px;
 background-color: rgb(141, 141, 141);
}

img {
  width: 45%;
  height: 100%;
}

#ff {
  width: 55%;
  padding: 10px;

}

.subCont, .subCont b, .subCont p, .subCont h3, #ff {
  background-color: rgb(158, 158, 158);
}

footer, footer b, footer p, footer h3, footer h3 a {
  background-color: rgb(54, 54, 54)  ;
  text-decoration: none;
  color: rgb(158, 158, 158)     ;
}

footer {
  min-width: 100%;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;


}

footer h3 {
  font-size: 24px;
margin-bottom: 12px;
}

@media (max-width: 550px) {
.titulo {
  font-size: 30px;

}
}